.container {
   display: flex;
   width: 200px;
   height: 200px;
   background: #eee;
   border-radius: 20px;
   padding: 20px;
   flex-direction: column;
   align-items: center;
   justify-content: space-between;
}
.compas {
   width: 140px;
   height: 140px;
   background: url("./../../../../../images/klipartz.com.png") center center no-repeat;
   background-size: cover;
   position: relative;
}
.compas span {
   display: block;
   z-index: 1;
   background: url("./../../../../../images/28.png") center center no-repeat;
   background-size: contain;
   width: 50px;
   height: 60px;
   position: absolute;
   top: 50%;
   left: 50%;
}
.desc {
   font-style: normal;
   font-size: 1.1em;
   font-weight: 800;
}
